.loginHolder {
    max-width: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    margin: 25% auto;
    border-radius: 15px;
    gap: 20px;
    box-shadow: rgb(0 0 0 / 24%) 8px 12px 8px 2px;
}

.addUserForm {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    width: 100%;
    min-height: 15vh;
    justify-content: flex-end;
}

.addUserForm label {
    display: flex;
    justify-content: space-between;
    gap: 15px;
    align-items: center;
    margin: 10px auto;
    font-size: 18px;
    max-width: 450px;
    width: 100%;
}

.addUserForm input {
    max-width: 450px;
    width: 100%;
    text-indent: 5px;
    font-style: italic;
    padding: 8px 5px;
    font-size: 15px;
    font-weight: 500;
    color: gray;
    border: 1px solid rgb(197 197 197);
}

.loginHolder button {
    background-color: #91B7CE;
    max-width: 180px;
    width: 100%;
    padding: 10px 0px;
    border-radius: 5px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    color: white;
    border: none;
    margin-top: 20px;
    max-width: 450px;
    width: 100%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.loginHolder button:hover {
    background-color: #4e616d;
    color: white;
}

.enjoyLife {
    color: #595f63;
    font-style: italic;
    font-weight: 400;
    margin: 30px auto;
    
}

.none {
    display: none;
}

.klasaNema {
    display: none;
}

.spanWarning {
    max-width: 500px;
    width: 100%;
    display: flex;
    justify-content: center;
    color: coral;
    font-weight: bold;
    margin: 0px auto;
}