.holder-centralni-dio {
    margin: 20px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    max-width: 1920px;
    width: 100%;
}

.usersDashboard {
    max-width: 2000px;
    height: 58vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #dce2e7e6;
    border-radius: 10px;
    box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
    flex-wrap: wrap;
    align-content: stretch;
    align-items: center;
}    

.usersSubtitles {
    display: flex;
    width: 100%;
    border-bottom: 1px solid #8080807a;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: 10px;
    margin-top: 85px;
}

.usersSubtitles h4 {
    max-width: 200px;
    width: 100%;
    text-align: center;
    margin-bottom: 0px;
}

.none {
    display: none;
}

.onlyForMainHeadingTitle {
    display: flex;
    max-width: 2000px;
    width: 100%;
    flex-direction: column;
    justify-content: space-around;
    margin: -20px auto;
}

